.background {
  width: 100%;
  height: 100%;

  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-color: var(--white);
}
.background--image {
  width: auto;
  height: 160px;
  object-fit: cover; 
}
.background > p {
  text-align: center;
}